
.card {
  display: flex;
  flex-direction: column;
  /* flex: 1 1 auto; */
  flex: 1;
  align-content: center;
  align-items: center;
  justify-content: center;    
  /* background-color: #212121;     */
  background-color: var(--card-bg-color);
  margin-bottom: 20px;   
  /* min-width: 50%; */
  padding: 15px 10px 15px 10px;
  
  max-width: 24em;
  height: 30.25em;
  border-radius: 1em;
  /* border-color: rgb(162, 228, 30);
  border-width: 3px;
  border-style: solid; */   
}  

  .cardBlock {
    /* background-color: #00ADB5; */
    display: flex;
    flex-direction: column;
    flex: 1 1 auto;
    font-size: 1.2rem;
    padding: 5px 1px 5px 1px;    
    background-color: var(--card-bg-color);
    /* background-color: #212121; */
    /* border-color: rgb(233, 60, 11);
    border-width: 1px;
    border-style: solid; */
    margin: 3px 1px 5px 1px;
    height: 26%;
  }

  .cardBlockData {
    /* background-color: #00ADB5; */
    background-color: var(--card-bg-color);
    
    display: flex;
    flex-direction: column;
    flex: 1 1 auto;
    font-size: 1.2rem;
    padding: 3px 1px 5px 1px;
    text-align: center;
    align-content: center;
    justify-content: top;

    margin: 5px 1px 3px 1px;
    overflow: auto;
    height: 60%;
    border-color: #3C6562;
    border-width: 1px;
    border-style: solid;
  }

  .cardName {    
    font-family: monospace;
    font-size: 1.2em;
    color: #fef4f4;
    padding: 5px 5px 0px 5px;
    text-align: center;
    align-content: center;
    justify-content: center;
    /* background-color: #212121;      */
    font-weight: 500; 
    background-color: var(--card-bg-color);
    margin: 1px 1px 1px 1px;
  }
  .cardQuad {    
    font-size: 0.95em;
    color: #e1ecec;
    padding: 5px 5px 0px 5px;
    text-align: center;
    align-content: center;
    justify-content: center;
    /* background-color: #212121;      */
    font-weight:300; 
    background-color: var(--card-bg-color);
  }

  .cardPrice {    
    font-size: 1.4rem;
    color: #8cff00ac;
    padding: 5px 5px 5px 5px;
    text-align: center;
    align-content: center;
    justify-content: center;
    background-color: var(--card-bg-color);
    font-weight: bold;  
  }
  
  .saleLink {
    background-color: var(--card-bg-color);
    color: #00FFF5;
    display: inline-block;
    padding: 1px 5px 5px 5px;
    font-size: 0.9em;
}

.cardTitle {
    background-color: var(--card-bg-color);
    font-size: 1.1rem;
    font-weight: bold;   
  }


 
 
   
