  .Common {   
    /* box-shadow: inset 0 0 10px gray; */
    box-shadow: 0px 1px 2px 0px rgba(246, 247, 245, 0.7),
    1px 2px 4px 0px rgba(246, 247, 245, 0.7),
    2px 4px 8px 0px rgba(246, 247, 245, 0.7);
    /* 2px 4px 16px 0px rgba(143, 255, 78, 0.7); */
   }
 
   .Uncommon {   

     /* box-shadow: inset 0 0 10px green; */
     box-shadow: 0px 1px 2px 0px rgba(143, 255, 78, 0.7),
    1px 2px 4px 0px rgba(143, 255, 78, 0.7),
    2px 4px 8px 0px rgba(143, 255, 78, 0.7);
    }
 
   .Rare {
    box-shadow: 0px 1px 2px 0px var(--blue-shadow),
    1px 2px 4px 0px var(--blue-shadow),
    2px 4px 8px 0px var(--blue-shadow);
   }
 
   .Epic {
    box-shadow: 0px 1px 2px 0px var(--purple-shadow),
    1px 2px 4px 0px var(--purple-shadow),
    2px 4px 8px 0px var(--purple-shadow);
   }
 
   .Legendary {
    box-shadow: 0px 1px 2px 0px var(--yellow-shadow),
    1px 2px 4px 0px var(--yellow-shadow),
    2px 4px 8px 0px var(--yellow-shadow);
   }

   Div::-webkit-scrollbar {
    width: 12px;
  }
  
  Div::-webkit-scrollbar-track {
    box-shadow: inset 0 0 2px rgb(108, 101, 101); 
    border-radius: 10px;
  }

  
  
  Div::-webkit-scrollbar-thumb {
    
    border-radius: 10px;
    box-shadow: inset 0 0 10px rgba(0, 0, 255, 0.46); 
  }

  .UncommonThumb::-webkit-scrollbar-thumb{
    border-radius: 10px;
    box-shadow: inset 0 0 10px var(--green-shadow); 
  }
  
  .RareThumb::-webkit-scrollbar-thumb{
    border-radius: 10px;
    box-shadow: inset 0 0 10px var(--blue-shadow); 
  }