
  .filtersDiv {   
    display: flex;
    justify-content: center;
    flex-direction: row;
    padding-top: 15px;
    padding-bottom: 15px;
    /* border-color: rgb(84, 233, 241);
    border-width: 2px;
    border-style: solid;     */
  }

  @media (max-width: 800px) {
    .filtersDiv {
      display: flex;
      justify-content: center;
      flex-direction: column;
      font-size: 1em;
    }    
  }


  .filtersDivChild{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin-bottom: 2px;
    /* border-color: chartreuse;
    border-width: 1px;
    border-style: dotted; */
  } 

  .dropDown {
    /* background-color: #064663; */
    background-color: var(--main-bg-color);
    overflow: hidden; 
    color: var(--text-color);
    flex: 1;
    font-size: 1.4em;
    margin-top: 5px;
    margin-bottom: 5px;
    margin-right: 10px;
    margin-left: 10px;
    padding: 5px 20px 5px 20px;
    text-align: center;
    width: 12em;
    border-width: 0.1em;
    border-color: var(--green-color);
    
    /* border-radius: 5px;
    border: 0px solid #DDDDDD;     */
  }

  .dropDownOption {
    background-color: #064663;
    overflow: hidden; 
    color: #ECB365;    
    font-size: 0.8em;    
    text-align: justify;
    width: 12em;
    /* border-radius: 5px;
    border: 0px solid #DDDDDD;     */
  }

  option {
    background-color: var(--main-bg-color);
    overflow: hidden; 
    color: var(--text-color);    
    font-size: 0.8em;    
    text-align: justify;
    width: 12em;
    /* border-radius: 5px;
    border: 0px solid #DDDDDD;     */
  }

  .labelDpMenu {
    font-size: 1.3em;
    padding: 25px 5px 1px 30px;
  }
  
  .inputText {
    background-color: var(--main-bg-color);
    color: var(--text-color);
    border-color: var(--green-color);
    flex: 1;
    font-size: 1.3rem;
    margin-top: 5px;
    margin-bottom: 5px;
    margin-right: 10px;
    margin-left: 10px;
    padding: 5px 20px 5px 25px;
    text-align: center;
    width: 10em;
    font-weight: bold;
  }    
  
  .searchBtn {
    cursor: pointer;
    display: grid;
    color:var(--green-color);
      place-items: center;
      font-size: 1.2rem;       
      text-align: center;
      font-weight: bold; 
      border-radius: 0.5em; 
      height: 3em;
      width: 5em;
      padding: 0px;
      outline-color: var(--green-color);
      outline-style: solid;
      outline-width: 0.1em;
      background-color: var(--main-bg-color); 
      border-width: 0px;
      box-shadow: 0px 1px 2px 0px rgba(0,255,255,0.7),
            1px 2px 4px 0px rgba(0,255,255,0.7);
            /* 2px 4px 8px 0px rgba(0,255,255,0.7); */
            /* 2px 4px 16px 0px rgba(0,255,255,0.7); */
  }    

  .searchBtnDiv{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin-bottom: 2px;
    /* border-color: chartreuse;
    border-width: 1px;
    border-style: dotted; */
  }
  
  .labelGhost {
    height: 1.3em;
    padding: 25px 5px 1px 30px;
  }





