
* {
    font-family: Arial, sans-serif, Helvetica ;
    font-size: 12px;
    color: var(--text-color);
    background-color: var(--main-bg-color);
    margin-top: 0px;
    padding: 1px;
  }

  .swal2Border{
    border-color: var(--green-color) !important;
    border-width: 1.2px !important;
    border-style: solid !important;
    
  }

  :root{
    --main-bg-color: #232931;
    --card-bg-color: #393E46;
    --green-color: #4ECCA3;
    --call-action-color: #de5c5cef;
    --call-action-hover: #ED6363;
    --text-color: #EEEEEE;
    --green-shadow: rgba(143, 255, 78, 0.7);
    --blue-shadow: rgba(46, 144, 248, 0.7);
    --purple-shadow: rgba(230, 110, 248, 0.7);
    --yellow-shadow: rgba(239, 255, 92, 0.7);
  }
  body {
    padding: 20px;
  }
 
  .main-div{
    display: flex;
    flex-direction: column;
    /* justify-content: center; */
    align-items: center;
    margin-top: 0px;
    
    
  }

  .float-div{
    position: sticky;
    width: 100vw;
    top: 0;
    /* margin-top: 1px;
    margin-bottom: 10px; */
  }

  option {
    background-color: #064663;
    overflow: hidden; 
    color: #ECB365;    
    font-size: 0.8em;    
    text-align: justify;
    width: 12em;
    /* border-radius: 5px;
    border: 0px solid #DDDDDD;     */
  }

section {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  overflow: hidden; 
  justify-content: center;
  
  /* width: 90%; */
}
article {
  display: flex;
  flex: 1;
  margin:5px 5px;	
  max-width: 26em;
  /* border-color: rgb(92, 147, 223);
  border-width: 3px;
  border-style: solid; */
  justify-content: center; 
}  
  
  input {
    background-color: white;
    border-radius: 5px;
    border: 1px solid rgb(97, 32, 158);
    color: black;
    font-size: 1.2rem;
    outline: none;
    padding: 10px 15px;
    width: 100%;
  }

  
  h1 {
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: 1.8em;
    font-size: 2.4em;
    margin-bottom: 10px;
    margin-top: 0px;
    text-align: center;
    color: rgb(39, 230, 230);
  }
  
  h2 {
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: 1.8em;
    margin-bottom: 4px;
    text-align: center;
    color: goldenrod;
  }
  
  h3 {
    font-size: 3rem;
    margin-bottom: 5px;
  } 

  
  .credits {

    font-size: 0.8rem;
    text-align: center;
    padding: 5px 20px 0px 20px;
  }
  
  .btn-flotante {
    display: grid;
    place-items: center;
    font-size: 1rem; 
    text-transform: uppercase; 
    text-align: center;
    font-weight: bold; 
    border-radius: 50%; 
    height: 3em;
    width: 3em;
    outline-style: none;
    /* letter-spacing: 2px;  */
    /* background-color: #E91E63;  */
    background-color: var(--call-action-color);
    padding: 1em 1em; 
    position: fixed;
    bottom: 40px;
    right: 40px;
    transition: all 300ms ease 0ms;
    box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
    z-index: 99;
  }
  /* .btn-flotante:hover {
    background-color: #2c2fa5; 
    box-shadow: 0px 15px 20px rgba(0, 0, 0, 0.3);
    transform: translateY(-7px);
  } */

   @media (hover: hover) {
    .btn-flotante:hover {
      background-color: var(--call-action-hover);
      box-shadow: 0px 15px 20px rgba(0, 0, 0, 0.3);
      transform: translateY(-7px);
    }
  } 

  @media only screen and (max-width: 600px) {
     .btn-flotante {
      background-color: var(--call-action-color);  
      font-size: 1em;
      padding: 1em 1em;
      bottom: 20px;
      right: 20px;       
    }

    .btn-flotante:hover {
      background-color: var(--call-action-hover);  
      -webkit-tap-highlight-color: transparent;         
      box-shadow: 0px 15px 20px rgba(0, 0, 0, 0.3);
      /* transform: translateY(-7px);         */
    }
  }

  a{
    text-decoration: none;
  }

input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none; 
}

div::-webkit-scrollbar {
  width: 12px;
}

div::-webkit-scrollbar-track {
  box-shadow: inset 0 0 2px rgb(108, 101, 101); 
  border-radius: 10px;
}

div::-webkit-scrollbar-thumb {
  border-radius: 10px;
  box-shadow: inset 0 0 10px var(--green-color); 
}

select::-webkit-scrollbar {
  width: 12px;
}

select::-webkit-scrollbar-track {
  box-shadow: inset 0 0 2px rgb(108, 101, 101); 
  border-radius: 10px;
}

select::-webkit-scrollbar-thumb {
  border-radius: 10px;
  box-shadow: inset 0 0 10px var(--green-color); 
}


.logo-discord{
  width: 2em;
}


