.cardP {    
    font-size: 1.1rem;
    padding: 1px 5px 1px 5px;
    text-align: center;
    align-content: center;
    justify-content: center;
    /* background-color: #212121;        */
    background-color: var(--main-bg-color);
  }

  .buildingName {    
    font-size: 1.1rem;
    padding: 1px 1px 1px 1px;
    text-align: left;
    height: 1em;
    /* align-content: left;
    justify-content: left; */
    /* background-color: #212121;        */
    background-color: var(--card-bg-color);
  }

  .qtyValue {    
    font-size: 1.1rem;
    padding: 1px 5px 1px 5px;
    text-align: right;    
    background-color: var(--card-bg-color);
  }

  .column {
    display: flex;
    flex-direction: column;
    flex: 1;
    /* background-color: #212121;  */
    padding-bottom: 1px;
    background-color: var(--card-bg-color);
    /* border-color: rgb(211, 244, 22);
    border-width: 1px;
    border-style: solid;    */
    
  }

  .columnBuilding {
    display: flex;
    flex-direction: column;
    flex: 1;    
    /* background-color: #212121;  */
    padding-bottom: 1px;
    height: 2em;
    background-color: var(--card-bg-color);
    /* border-color: rgb(211, 244, 22);
    border-width: 1px;
    border-style: solid;    */
    min-width: 70%;
    
  }
  .mutableData {    
    display: flex;
    flex-direction: row;
    font-size: 0.8em;
    padding: 1px 2px 1px 2px;
    /* align-items: center;    */
    text-align: center;
    /* align-content: center; */
    justify-content: center;
    /* background-color: #212121;     */
    background-color: var(--card-bg-color);
    min-width: 20em;
    height: 2.2em;
    /* border-color: rgb(36, 220, 165);
    border-width: 1px;
    border-style: solid;    */
  }  
  
  

